.page{
    position: fixed;
    z-index: -1;
    width: 100vw;
    height: 100vh;
    background-color: white;
}

.navBar{
    top: 0;
    height: 60px;
    width: 100vw;
    box-shadow: 0 0 5px black;
    background-color: #00a8fc
}

.momentTime{
    position: absolute;
    top: 6px;
    right: 20px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(255, 255, 255);
}

.momentDate{
    position: absolute;
    top: 6px;
    left: 60px;
    font-size: 30px;
    font-weight: bold;
    color: rgb(255, 255, 255);
}

.settingsButton{
    outline: none;
    box-shadow: none;
    font-size: 20px !important;
    position: relative;
    border: none !important;
    top: 13px;
    background: transparent !important;
    left: 10px;
}

.newsDiv{
    position: absolute;
    top: 90px;
    right: 5vw;
    height: auto;
    width: calc(45% - 40px);
    box-shadow: 5px 5px 5px #00000033, -10px -10px 5px #ffffff4d;
}

.fillerDiv{
    position: absolute;
    top: 103%;
    height: calc(30% - 4vw);
    width: 100%;
    box-shadow: 0 1px 2px 0 rgba(60,64,67,0.302), 0 2px 6px 2px rgba(60,64,67,0.149);
}

.card-img-top{
    max-height: 13vh;
    overflow: hidden;
    object-fit: cover;
}

.card-text {
     overflow: hidden;
     display: -webkit-box;
     -webkit-line-clamp: 2;
     -webkit-box-orient: vertical;
     font-size: 10px;

}

.card-title{
    overflow: hidden;
    font-size: 15px;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
}

.h5{
    font-size: 15px;
}

.currencyContainer{
    margin-top: 20vh;
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 5vw;
    padding: 0 5vw;
    width: 90vw;
    height: auto;
    background: #e8e8e8;
    margin: 18vh auto 50px auto;
}


.rounded{
    border-right: 1px solid #aaa;
    float: left;
    margin-right: 10px;
}

.myModal{
    width: 90vw !important;
    max-width: none !important;
    color: #444;
}

.modal-title{
    font-size: 40px !important;
}

.input-group-lg{
    width: 50% !important;
    height: calc(1.5em + 1rem + 15px) !important;
}

.btn-primary{
    font-size: 12px!important;
}